.testimonial-section {
    width: 100%;
    height: auto;
    background-color: #00C2FFC2 !important;

    .testimonial-container {
        width: 1180px;
        height: auto;
        margin: 0 auto;
        margin-top: 2rem;
        padding: 2rem 0;
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .upper {
            width: 100%;
            height: auto;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .section-title {
                color: #000;
                font-weight: 500;
                font-size: 2.5rem;
                display: flex;
            }

            .additional {
                position: relative;
                width: 50%;
                height: auto;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                gap: 5px;

                .image {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 5px;
                    background-color: #fff;
                    border: none;
                    outline: none;
                }
            }
        }

        .middle {
            width: 100%;
            height: auto;

            .review-link {
                color: #000 !important;
                width: 100%;
                height: auto;

                .text {
                    font-size: 1.3rem;
                    font-weight: 500;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                }
            }
        }

        .bottom {
            width: 100%;
            height: auto;
            margin: 0 auto;

            .testimonial-content {
                .testimonial-content-wrapper {
                    background-color: #fff;
                    width: 100%;
                    height: auto;
                    border-radius: 0;
                    margin: 0 auto;
                    padding: 1rem;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    gap: 20px;

                    .image-content {
                        .img {
                            width: 230px;
                            height: 230px;
                            border-radius: 60px;
                            object-fit: cover;
                        }
                    }

                    .text-content {
                        width: 100%;
                        height: auto;
                        margin: 1rem;
                        line-height: 30px;
                        font-size: 1.3rem;
                        position: relative;
                        display: flex;
                        flex-direction: column;
                        gap: 2rem;

                        .quotes {
                            padding: 0 2rem;

                            &:before {

                                content: url("https://admin.aventuras.co.in/uploads/quoteicon_90f33e35d5_3474151632.png");
                                position: absolute;
                                top: -15px;
                                left: 10px;
                            }

                            &:after {
                                content: url("https://admin.aventuras.co.in/uploads/quoteicon_90f33e35d5_3474151632.png");
                                position: absolute;
                                bottom: 45px;

                                transform: rotate(180deg);
                            }

                        }

                        .author {
                            display: flex;
                            justify-content: flex-end;
                            align-items: center;
                            font-size: 1.5rem;
                        }
                    }
                }
            }
        }
    }

    @media (max-width:1024px) {
        .testimonial-container {
            width: 768px;
            height: auto;
            margin: 0 auto;
            margin-top: 2rem;
            padding: 2rem 1rem;
            display: flex;
            flex-direction: column;
            gap: 2rem;

            .upper {
                .section-title {
                    font-size: 2.2rem;
                }

                .additional {
                    width: 30%;
                }
            }

            .bottom {
                .testimonial-content {
                    width: 100%;
                    margin: 0 auto;

                    .testimonial-content-wrapper {
                        padding: 1rem;
                        gap: 20px;

                        .image-content {
                            .img {
                                width: 170px;
                                height: 170px;
                                border-radius: 60px;
                                object-fit: cover;
                            }
                        }

                        .text-content {
                            font-size: 1.5rem;
                            gap: 1rem;

                            .quotes {
                                padding: 0 2rem;

                                &:before {

                                    content: url("https://admin.aventuras.co.in/uploads/quoteicon_90f33e35d5_3474151632.png");
                                    position: absolute;
                                    top: -15px;
                                    left: 10px;
                                }

                                &:after {
                                    content: url("https://admin.aventuras.co.in/uploads/quoteicon_90f33e35d5_3474151632.png");
                                    position: absolute;
                                    bottom: 45px;
                                    transform: rotate(180deg);
                                }
                            }

                            .author {
                                display: flex;
                                justify-content: flex-end;
                                align-items: center;
                                font-size: 1.8rem;
                            }
                        }
                    }
                }

            }
        }
    }

    @media (max-width:767px) {
        .testimonial-container {
            width: 100%;
            max-width: 370px;
            min-width: 300px;
            height: auto;
            margin: 0 auto;
            padding: .5rem 1rem;
            gap: 0.5rem;

            .upper {
                padding: 0;
                height: auto;
                width: 100%;
                margin: 0 auto;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .section-title {
                    font-size: 1.2rem;
                    padding: 1rem 0;

                }

                .additional {
                    position: relative;
                    width: 50%;
                    height: auto;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    gap: 5px;

                    .image {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 5px;
                        background-color: #fff;
                        border: none;
                        outline: none;
                    }
                }
            }

            .middle {
                width: 100%;
                height: auto;

                .review-link {
                    color: #000 !important;
                    width: 100%;
                    height: auto;

                    .text {
                        font-size: 1rem;
                        font-weight: 500;
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                    }
                }
            }

            .bottom {
                padding: 0;
                height: auto;
                width: 100%;
                margin: 0 auto;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .testimonial-content {
                    .testimonial-content-wrapper {
                        flex-direction: column;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: .6rem !important;

                        .image-content {
                            display: flex;
                            justify-content: center;
                            align-items: center;

                            .img {
                                margin: 0 auto;
                                width: 135px;
                                height: 135px;
                                border-radius: 50px;
                            }
                        }

                        .text-content {
                            width: 100%;
                            height: auto;
                            margin: .5rem;
                            line-height: 30px;
                            font-size: 1rem;
                            position: relative;
                            display: flex;
                            flex-direction: column;
                            gap: 1.5rem;

                            .quotes {
                                padding: 0 2rem;

                                &:before {
                                    content: url("https://admin.aventuras.co.in/uploads/quoteicon_90f33e35d5_3474151632.png");
                                    position: absolute;
                                    top: -14px;
                                    left: 10px;
                                }

                                &:after {
                                    content: url("https://admin.aventuras.co.in/uploads/quoteicon_90f33e35d5_3474151632.png");
                                    position: absolute;
                                    bottom: 45px;
                                    transform: rotate(180deg);
                                }
                            }

                            .author {
                                display: flex;
                                justify-content: flex-end;
                                align-items: center;
                                font-size: 1rem;
                            }
                        }
                    }
                }
            }
        }
    }
}